<template>
  <!-- Slideshow container -->
  <div class="slideshow">
    <div class="slideshow-container">
        <div v-for="pic in photoList" :key="pic.src" class="mySlides fade" ref="slides">
            <div class="numbertext">{{slideIndex}}/{{ photoList.length }}</div>
            <img :src="pic.src" style="width: 100%; height: 500px; object-fit: scale-down;">
            <div class="text">{{ pic.caption }}</div>
        </div>
        <!-- Next and previous buttons -->
        <a class="prev" @click="plusSlides(-1)">&#10094;</a>
        <a class="next" @click="plusSlides(1)">&#10095;</a>
               
        </div>
        <br>

        <!-- The dots/circles -->
        <div style="text-align:center">
        <span ref="dot" @click="currentSlide(1)"></span>
        <span ref="dot" @click="currentSlide(2)"></span>
        <span ref="dot" @click="currentSlide(3)"></span>
        </div>
    </div>
</template>

<script>
import { event } from 'vue-gtag'

export default {
    props: {
        photoList: Array[Object] // [{src: "source", "caption": "####"}]
    },

    data() {
        return {
            slideIndex: 1,
        }
    },
    mounted() {
        this.showSlides(this.slideIndex);
    },
    methods: {

        // Next/previous controls
        plusSlides(n) {
            this.showSlides(this.slideIndex += n);
            event('photoSlide', { "slideDirection": n })

        },

        // Thumbnail image controls
        currentSlide(n) {
            this.showSlides(this.slideIndex = n);
        },

        async showSlides(n) {
            let i;
            let slides = this.$refs.slides;
            let dots = this.$refs.dot;

            if (n > slides.length) {this.slideIndex = 1}
            if (n < 1) {this.slideIndex = slides.length}
            for (i = 0; i < slides.length; i++) {
                slides[i].style.display = "none";
            }
            for (i = 0; i < dots.length; i++) {
                console.log(dots)
                dots[i].className = dots[i].className.replace(" active", "");
            }
            slides[this.slideIndex-1].style.display = "block";
            dots[this.slideIndex-1].className += " active";
            }
    }

}
</script>

<style scoped>
* {box-sizing:border-box}


.slideshow {
    display: flex;
    justify-content: center;
    background: rgba(255, 255, 255, 0.2);
}

/* Slideshow container */
.slideshow-container {
  position: relative;
  margin: auto;
}

.slideshow-container img {
    width: 90%;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}
</style>


