<template>
    <div v-if="showBanner" class="cookie-banner">
        <p>Es werden Cookies (Google Analytics) verwendet, um diese Seite sowie die Nutzerfreudnlichkeit stetig zu verbessern. Bist du mit der Nutzung unserer Cookies einverstanden? </p>
        <button @click="setCookie()">Erlauben</button>
        <button @click="deleteCookie()">Ablehnen</button>
    </div>
  </template>
  
  <script>
  import { useCookies } from "vue3-cookies";
  const { cookies } = useCookies();
  export default {
    mounted() {
      this.getCookie()
    },
    data() {
      return {
        cookies: cookies,
        showBanner: true,
      }
    },
    methods: {
        async setCookie() {
            console.log(this.cookies)
            await this.cookies.set("ga_accpeted", "True");
            this.showBanner = false;
        },
        getCookie() {
            console.log(this.cookies)
            const value = this.cookies.get('ga_accpeted');
            if (value) {
                this.showBanner = false;
            } else {
                this.showBanner = true;
            }
        }
    },
  }
  </script>
  
  <style>
  .cookie-banner {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 16px;
    background-color: #f1f1f1;
    border-top: 1px solid #ccc;
    text-align: center;
    z-index: 9999999999;
  }
  .cookie-banner p {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 8px;
  }
  .cookie-banner button {
    background-color: #0077c2;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    margin-right: 8px;
    cursor: pointer;
  }
  .cookie-banner button:hover {
    background-color: #005fa6;
  }
  </style>
  