<template>
  <div class="app">
    <router-view @delete-cookie="this.deleteCookie()"></router-view>
    <CookieBanner />
    <footer>
      <router-link to="/impressum">Impressum</router-link>
      <router-link to="/impressum">Datenschutz</router-link>
      <router-link to="/cookiesPolicy">Cookie Einstellunfen</router-link>
    </footer>


  </div>
</template>

<script>
import CookieBanner from './components/cookieBanner.vue';
import { useCookies } from "vue3-cookies";
const { cookies } = useCookies();
export default {
  name: 'App',
  components: {
    CookieBanner
  },
  data() {
    return {
      cookies: cookies,
    }
  },
  methods: {
    async deleteCookie() {
        if (this.getCookie()) {
          await this.cookies.set('ga_accpeted', "False");
        }
    },
    getCookie() {
      const value = this.cookies.get('ga_accpeted');
      if (value) {
        return true
      } else {
        return false
      }
    }
  }
  
}
</script>

