<template>
    <div class="nav-bar-container">
        <div class="nav-toggle">
            <div class="right">
                <a href="/">Julia Zasada Fotografie & Design</a>
            </div>
            <div class="left">
                <button @click="expandMenu()" id="nav-toggle-btn"><font-awesome-icon icon="fa-solid fa-bars" /></button>
            </div>
        </div>
        <div v-if="expandedMenu" id="menu-expand">
            <div class="upper">
                <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('home')" id="home">Home</button>
                <button v-if="!this.showSubNavFlag" class="nav-item" @click="toggleSubNav('angebote')" :class="setElementActive('angebote')" id="angebote">Angebote <font-awesome-icon icon="fa-solid fa-chevron-down" /></button>
                <button v-else class="nav-item" @click="toggleSubNav('angebote')" :class="setElementActive('angebote')" id="angebote">Angebote <font-awesome-icon icon="fa-solid fa-chevron-up" /></button>
                <div class="lower" v-if="this.showSubNavFlag">
                    <button class="nav-item" :class="setElementActive('angebote')" id="angebote">People</button>
                    <button class="nav-item"  :class="setElementActive('angebote')" id="angebote">Tiere</button>
                    <button class="nav-item"  :class="setElementActive('angebote')" id="angebote">Events</button>
                </div>
                <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('portfolio')" id="portfolio">Portfolio</button>
                <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('kontakt')" id="kontakt">Kontakt</button>
            </div>        
        </div>

        <nav class="nav-bar">
            <div class="upper">
                <a href="#">Julia Zasada Fotografie & Design</a>
                <div class="main right">
                    <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('home')" id="home">Home</button>
                    <button v-if="!this.showSubNavFlag" class="nav-item" @click="toggleSubNav('angebote')" :class="setElementActive('angebote')" id="angebote">Angebote <font-awesome-icon icon="fa-solid fa-chevron-down" /></button>
                    <button v-else class="nav-item" @click="toggleSubNav('angebote')" :class="setElementActive('angebote')" id="angebote">Angebote <font-awesome-icon icon="fa-solid fa-chevron-up" /></button>
                    <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('portfolio')" id="portfolio">Portfolio</button>
                    <button class="nav-item" @click="scrollToElement($event)" :class="setElementActive('kontakt')" id="kontakt">Kontakt</button>
                </div>
            </div>
            <div class="lower" v-if="this.showSubNavFlag">
                <button class="nav-item" :class="setElementActive('angebote')" id="angebote">People</button>
                <button class="nav-item" :class="setElementActive('angebote')" id="angebote">Tiere</button>
                <button class="nav-item" :class="setElementActive('angebote')" id="angebote">Events</button>
            </div>
        </nav>
    </div> 
</template>

<script>
import { event } from 'vue-gtag'
export default {
    data() {
        return {
            active: "home",
            expandedMenu: false,
            showSubNavFlag: false,
        }
    },
    methods: {
        setElementActive(id) {
            if(id == this.active) {
                return "active"
            } else {

                return "inactive"
            }
        },
        scrollToElement(ev) {
            // Hide Toggled Menu
            this.showSubNavFlag = false
            this.expandedMenu = false
            var id = ev["target"].id
            this.active = id

            event('nav-bar', { "clicked": id })
            this.$emit('id', id);
        },
        expandMenu() {
            this.expandedMenu = !this.expandedMenu
        },
        toggleSubNav(nav) {
            if (nav == "angebote") {
                this.showSubNavFlag = !this.showSubNavFlag
            }
        }
    }
}
</script>

